.App {
  display: grid;
  grid-template-rows: 15% max-content 10%;
  height: 100%;
}
body {
  margin: 0;  
  height: 100%;
  width: 100%;
}

html {
  margin: auto;
  display:table;
  background-color: burlywood;
  height: 100%;
  width: 100%;
  /* overflow: hidden; */
}

#root {
  height: 100%;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
